
document.addEventListener('DOMContentLoaded', function(){
    let preloader               = document.querySelector('.jsPreloader');
    let preloaderPercentage     = document.querySelector('.jsPreloaderPercentage');
    let imagesCount             = document.querySelectorAll('img').length;
    let body                    = document.querySelector('body');
    let percent                 = 100 / imagesCount;
    let progress                = 0;
    let loadedImg               = 0;

    setTimeout(() => {
        for (var i = 0; i < imagesCount; i++) {
            var img_copy        = new Image();
            img_copy.src        = document.images[i].src;
            img_copy.onload     = img_load;
            img_copy.onerror    = img_load;
        }
    
    
        function img_load () {
            progress += percent;
            loadedImg++;
    
            preloaderPercentage.innerHTML = Math.round(progress) + ' %'
    
            console.log(Math.round(progress));
            
            if (progress >= 100 || loadedImg == imagesCount) {
       
                setTimeout(() => {
                    preloader.classList.add('is-loaded');
                    body.classList.add('is-loaded');
                }, 250);
                
            }
        }
    }, 500);

})